html, body, div, span, h1, h2, h3, 
h4, h5, h6, p, ol, ul, li, table,caption,    /* Reset CSS and put the margin, padding and */
tr, th, td, article,aside, canvas,           /* border to zero in all the HTML elements */
footer, header, menu, nav, section {    
	                              margin: 0;
						          padding:0;
								  border:0;
                                  }

								  
   
    body{
	    background-color: rgb(255,255,255);
		font-family: Verdana, Geneva,sans-serif; /*The font family has several */
	    font-size:16px;                         /* font names as a fallback system */
	}                                         /*This is done to avoid compability problems */
												  /*With older browsers and OS */
	
	 
	 
	#container{
		      
	            width:90%;                         /* Set the width of the container div to 905 of the viewport width */
	 	        height: auto;
				max-width: 1200px;                  /* But the maximum width will be 1200px, to prevent */
				background-color: rgb(255,255,255);	 /*   getting to wide on bigger screens i.e. JD monitors 1980px wide*/	                     
		        padding:0;  
		        margin: 0 auto;          /* The top and bottom margins are set to zero */
				border: 2px solid #00022a;  /* Left and Right margins to auto so the div */
	} 	                                    /* will be centered within the viewport */
		
/* -------------------- HEADER SECTION -------------------------------------*/	
	header{
		  
		    background-color: #00022a;   /* I set the properties for the header section */
		    height: 200px;
			width: 100%;
			margin:0;
			align-items:center;
	}
	 
	#prof_pic{
		       width:15%;
			   height: auto;
			   min-width: 75px;
			   margin:15px 30px 0 40px;
			   border: 6px solid white;
			   border-radius: 12px;
			   vertical-align: middle;
	}
	#maths_pic{
		width:95%;
		height: auto;
		display: block;
        margin: 30px auto;		
		border: 2px solid blue;
		border-radius: 12px;
	
	}
	#divHeader{
              display:inline;    /* I set display to inline so is in */
			  width:60%;         /* the same row as the profile picture */
			  position:absolute; /* I give absolute positioning */ 
              right:0;			  
			  animation:moveDivH 6s linear;
	}         		 
	
	#divHeader h1{
		       font-size:2em;
			   font-family: verdana;
			   color: white;
		       padding-top:80px;
               animation: changeColorH1 20s linear 8s infinite;			   
	}
	@keyframes moveDivH{     /* Animation to produce motion of moveDivH div tag and the H1 inside */
	                        0%{right:-1000px;}
		                    20%{right:-750px;}
		                    40%{right:-500px;}
		                    60%{right:-250px;}							   
		                    80%{right:0px;}							   
							90%{font-size:1.2em;}	
							100%{right:0px;
							     font-size:1em;}
	}
	  
	@keyframes changeColorH1{    /* Animation to produce change of colour of H1 inside moveDivH div tag*/
	                        0%{color:#ffffff;}
		                    20%{color:#ffaa44;}
		                    40%{color:#ff5511;}
		                    60%{color:#ff665a;}							   
		                    80%{color:#ffff44;}							   	
							100%{color:#ffffff;}
	}  
/*------------------------- END OF HEADER SECTION------------------------------------------*/

/*--------------------------NAV SECTION ------------------------------------------------*/	  
	nav{
		     
             width: 100%;              /* Setting for the nav elements that will contain the navigation bar */
			 margin:2em 0;
			 text-align: center;
			
    }

	nav ul {                    /*We set the style-type for list tag in navs to "none" */
            list-style-type: none;
            padding: 0 1em, 1em, 1em;
            background-color: white;
			text-align: center;		
    }
	
	nav li {   display: inline;    /*I apply inline to display list items in the same row */             
			   border-radius: 8px;
			   background-color:#d6d8db;
			   border: 2px solid blue;
			   padding:0.5em;  
			   margin: 0.2em;
			   font-size: 1em;
    }              
    nav a{
	     text-decoration: none;  /*When I set text-decoration to none */
	     padding:0.5em;           /* the link will not underline if the mouse hover */
	     margin: 0.2em;   
    }


    nav li:hover{
	      background-color: #353388;
	      color: white;
	      font-weight: bold;
    }
    a:hover{
	      background-color: #353388;
	      color: white;
		  font-weight: bold;
    }   
	
    .active {
      background-color: #94a6ef;  
	  color: black;
	  font-weight: bold;
    }
	
 /*-------------------------END OF NAV SECTION------------------------------*/ 

/*----------------------SECTION-----------------------------------------*/ 
	section{
		  
		  margin:1em;
          padding: 0.3em;
	      border: 3px solid #585858;
	      border-radius:15px; 
		  background-color: #94a6ef;	   
		  width:auto;	  
	}
/*--------------------END OF SECTION--------------------------------*/

/*---------------------ARTICLE-------------------------------------*/
	#article1, #article2, #article3, #article4{
		       height: auto;
	           width:70%;
			   padding:0.5em;
			   margin:0.5em;
			   border: 4px solid #585858;
			   border-radius:15px;
			   float: left;
			   background-color: #efffff;
	}
	
	#article3{
		   width: 95%;
		   float: none;
		   margin: 0.5em auto;
	}

	#article4 {
		width: 95%;	
		margin: 0.5em auto;
		padding:0.5em;
		margin:0.5em;
		float: none;		
	}

	
	article p {
		        padding:1em;
				padding-bottom:0;
				text-align: justify;
				line-height:180%;
				font-size:1.2em;
	}
	article a{
	          text-decoration: none;
			  color:blue;
			  font-weight: bold;
	}	
    article ul{
			font-size:1.2em;
            padding-left:3em;
			line-height:180%;
	}	   
	article h1{
	             text-align:center;
				 font-family:  "Times New Roman", Times, serif;
				 font-size:2em;
				 padding-top:1.5em;
	}
	article h3{
	              text-align:center;
				  font-size: 1.5em;
				  padding-top:1em;
	}	
	.imageCaption{
		font-family: "Arial", Verdana, serif;
		font-size:0.8em;
		font-weight: bold;
		color: blue;
		text-align: center;
	}
/*--------------------------------END OF ARTICLE-------------------------------------*/

/*--------------------------------IMAGES------------------------------------------*/	
    .imgLeft{
				 float:left;         /* Images of this class are floated to */
				 margin:0 1em 1em 0; /* the left  */
                 padding:1em;					 
    }  	   
    .imgRight{
				 float:right;        /* Images of this class are floated to */
				 margin:0 0 1em 1em; /* the right */
                 padding:1em;				 
    }  
    .imgCenter{
		     display:block;  /* Images are inline elements, I changed display to */
			 margin:0 auto;  /* block and then set margin to auto in order to */
	         padding:1em;    /* center the image inside the article  */
				  
	}
	.imgReact {
				display: block;
				border: 2px solid rgb(121, 121, 121);
				border-radius: 12px;
		             /* Images of this class are floated to */
			  	margin: 30px auto ; /* the left  */
                
	}  
    #article1 img, #article2 img, #article4 img{
               transition: width 2s, height 2s, transform 2s; /*Transition for images in articles/enlargement */
    }		                                                  /* over a period of 2 seconds */
	#article1 img:hover{          
	            width:300px;
                height:auto;			 /*When mouse hover images in articles, these enlarge in heigth and width */	
	}
    #article2 img:hover{
			    width:600px;
			    height:auto;
    }
/*-------------------END OF IMAGES------------------------*/






/*------------------- ASIDE SECTION -----------------------*/		 
	aside{
		   display: table-cell; 
		   background-color:#efffff;
		   width:20%;
		   padding:0.3em;
		   margin:0.5em;
		   right:0;   
		   float: right; 
		   border: 4px solid #585858;
		   border-radius:15px;
	}
    aside img{
		       display: block;
			   margin: 1.5em auto;
			   border: 3px solid #585858;
	}
    aside img:hover{ 
		         width:200px;
    }	 
	aside h3{
		   padding-top:1em;
		   padding-bottom:0.5em;
	}
	aside p{
	        font-size:1em;
			line-height:150%;
			padding-top:1em;
	}
/*--------------- END OF ASIDE --------------------------*/

/*--------------- FOOTER  ----------------------------*/	
footer{
    clear: both;
    width:95%;
    display:block;
     margin: 10px auto;
     border: 3px solid #C0C0C0;
     border-radius:15px;
     background-color: #FFFFFF;
     text-align:center;
     padding:5px;
 }			
 footer h1 {
    font-size: 1.5em;
    font-weight: bold;
    margin: 1px;
    padding: 1px;
 }
 footer h3{
     font-size: 1em;
    text-align:right;
 }
 
 footer p{
     text-align: left;
     padding-top: 1px;
 }
 /*----------------- END OF FOOTER ----------------------------*/
 
	
	


	  
